"use client";

import { useEffect, useState } from "react";
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";
import { Separator } from "@/components/ui/separator";
import {
  MapPin,
  ChevronRight,
  X,
  Users,
  LandPlot,
  Building2,
  TrendingUp,
  AlertCircle,
  CheckCircle,
  Leaf,
  Home,
  User,
  Shield,
  BarChart3,
} from "lucide-react";

/* ================= TYPES ================= */

type Field = {
  id: number;
  zone_ids: string;
  field_name: string;
  location: string;
  farmer_name: string;
  total_area: number;
  cultivatable_area: number;
  ownership_type_display: string;
  subadmin_name: string;
};

type ZoneCard = {
  id: number;
  name: string;
  location: string;
  totalArea: number;
  cultivatableArea: number;
  farmsCount: number;
  fields: Field[];
  efficiency: number;
  status: "active" | "moderate" | "low";
};

/* ================= COMPONENT ================= */

export function FarmOverview() {
  const [zones, setZones] = useState<ZoneCard[]>([
    { id: 1, name: "Zone-1", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
    { id: 2, name: "Zone-2", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
    { id: 3, name: "Zone-3", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
    { id: 4, name: "Zone-4", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
    { id: 5, name: "Zone-5", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
    { id: 6, name: "Zone-6", location: "—", totalArea: 0, cultivatableArea: 0, farmsCount: 0, fields: [], efficiency: 0, status: "low" },
  ]);

  const [loading, setLoading] = useState(true);
  const [selectedZone, setSelectedZone] = useState<ZoneCard | null>(null);
  const [stats, setStats] = useState({
    totalFarms: 0,
    totalArea: 0,
    cultivatableArea: 0,
    averageEfficiency: 0,
  });

  /* ================= FETCH ALL FIELDS ================= */

  const fetchAllFields = async (): Promise<Field[]> => {
    const token = localStorage.getItem("authToken");

    try {
      const res = await fetch("https://rda.ngrok.app/api/fields/", {
        headers: {
          Authorization: `Bearer ${token}`,
          "ngrok-skip-browser-warning": "true",
        },
      });

      const json = await res.json();
      return json.data || [];
    } catch (err) {
      console.error("Error fetching fields:", err);
      return [];
    }
  };

  /* ================= GROUP BY ZONE ================= */

  const groupByZone = (fields: Field[]) => {
    const map: Record<string, ZoneCard> = {};

    fields.forEach((f) => {
      const zone = f.zone_ids;

      if (!map[zone]) {
        map[zone] = {
          id: Number(zone.split("-")[1]),
          name: zone,
          location: f.location,
          totalArea: 0,
          cultivatableArea: 0,
          farmsCount: 0,
          fields: [],
          efficiency: 0,
          status: "low",
        };
      }

      map[zone].fields.push(f);
      map[zone].farmsCount += 1;
      map[zone].totalArea += Number(f.total_area || 0);
      map[zone].cultivatableArea += Number(f.cultivatable_area || 0);
    });

    Object.values(map).forEach((z) => {
      if (z.totalArea > 0) {
        z.efficiency = Math.round((z.cultivatableArea / z.totalArea) * 100);
        z.status =
          z.efficiency >= 80
            ? "active"
            : z.efficiency >= 60
            ? "moderate"
            : "low";
      }
    });

    return map;
  };

  /* ================= LOAD ================= */

  useEffect(() => {
    const load = async () => {
      setLoading(true);

      const fields = await fetchAllFields();
      const zoneMap = groupByZone(fields);

      let totalFarms = 0;
      let totalArea = 0;
      let cultivatableArea = 0;
      let totalEfficiency = 0;
      let count = 0;

      const updated = zones.map((z) => {
        if (zoneMap[z.name]) {
          const d = zoneMap[z.name];
          totalFarms += d.farmsCount;
          totalArea += d.totalArea;
          cultivatableArea += d.cultivatableArea;
          totalEfficiency += d.efficiency;
          count++;
          return { ...z, ...d };
        }
        return z;
      });

      setZones(updated);
      setStats({
        totalFarms,
        totalArea,
        cultivatableArea,
        averageEfficiency: count ? Math.round(totalEfficiency / count) : 0,
      });

      setLoading(false);
    };

    load();
  }, []);

  /* ================= UI ================= */

  if (loading) {
    return <p className="text-muted-foreground">Loading zones…</p>;
  }

  return (
    <div className="space-y-8">
      <h1 className="text-3xl font-bold">Farm Zones Overview</h1>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {zones.map((zone) => (
          <Card
            key={zone.id}
            className="cursor-pointer hover:shadow-xl"
            onClick={() => zone.farmsCount > 0 && setSelectedZone(zone)}
          >
            <CardHeader>
              <CardTitle className="flex justify-between">
                {zone.name}
                <Badge>{zone.farmsCount} Farms</Badge>
              </CardTitle>
              <CardDescription className="flex items-center gap-2">
                <MapPin size={14} />
                {zone.location}
              </CardDescription>
            </CardHeader>

            <CardContent className="space-y-3">
              <div className="flex justify-between text-sm">
                <span>Total Area</span>
                <span>{zone.totalArea} acres</span>
              </div>

              <Progress value={zone.efficiency} className="h-2" />

              <div className="flex justify-between text-xs">
                <span>Efficiency</span>
                <span>{zone.efficiency}%</span>
              </div>

              <Button
                variant="ghost"
                size="sm"
                className="w-full"
                disabled={zone.farmsCount === 0}
              >
                View Farms <ChevronRight size={16} />
              </Button>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* ================= MODAL ================= */}
      {selectedZone && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4">
          <div className="bg-white rounded-xl w-full max-w-3xl max-h-[90vh] overflow-hidden">
            <div className="flex justify-between p-4 border-b">
              <h2 className="text-xl font-bold">{selectedZone.name}</h2>
              <Button size="icon" variant="ghost" onClick={() => setSelectedZone(null)}>
                <X />
              </Button>
            </div>

            <div className="p-4 space-y-4 overflow-y-auto max-h-[70vh]">
              {selectedZone.fields.map((f) => (
                <Card key={f.id}>
                  <CardContent className="p-4 flex justify-between">
                    <div>
                      <p className="font-semibold">{f.field_name}</p>
                      <p className="text-sm text-muted-foreground">
                        Farmer: {f.farmer_name}
                      </p>
                      <p className="text-sm">
                        Ownership: {f.ownership_type_display}
                      </p>
                      <p className="text-sm">
                        Sub Admin: {f.subadmin_name}
                      </p>
                    </div>
                    <div className="font-bold">{f.total_area} acres</div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
